    opacity:1
  }

  70%{
    opacity:1
  }

  100%{
    opacity:0
  }
}

@keyframes fade{
  0%{
    opacity:0
  }

  30%{
    opacity:1
  }

  70%{
    opacity:1
  }

  100%{
    opacity:0
  }
}

.slider{
  background-color:#f1f1f1;
  overflow:hidden
}

.slider__item{
  height:500px;
  position:relative
}

.slider__item__info{
  background-color:rgba(0,0,0,0.3);
  position:absolute;
  color:#fff;
  padding:10%;
  inset:0
}

.slider__item__info h1{
  font-size:2em
}

.slider__item__info h3{
  font-size:1.3em
}

.slider__item__info button{
  padding:0.5em 1em;
  background-color:#333;
  color:#fff;
  margin:1em 0;
  text-transform:uppercase
}

.slider__item__info button:hover{
  background-color:#4b4b4b
}

.slider__item__btns{
  position:absolute;
  inset:85% 20% 3% 20%;
  width:auto;
  display:flex;
  justify-content:center;
  grid-gap:1em;
  gap:1em;
  padding:10px
}

.slider__item__image{
  height:100%
}

.slider__item__image img{
  min-width:100%;
  min-height:100%;
  object-fit:cover
}

.slider__button{
  display:inline-block;
  cursor:pointer;
  width:15px;
  height:15px;
  border-radius:50%;
  border:3px solid #333
}

.slider__button.active{
  background-color:#333
}

.slider__running{
  -webkit-animation-play-state:running;
  animation-play-state:running;
  -webkit-animation:fade 4s ease-in-out 0s infinite;
  animation:fade 4s ease-in-out 0s infinite
}

.slider__paused{
  -webkit-animation-play-state:paused;
  animation-play-state:paused
}

@media (max-width: 960px){
  .slider{
    height:400px
  }

  .slider__item__btns{
    inset:80% 20% 5% 20%
  }
}

@media (max-width: 760px){
  .slider{
    height:370px
  }

  .slider__item__btns{
    inset:60% 20% 5% 20%
  }
}

